
import { defineComponent } from 'vue'
import RoundText from '@/atoms/RoundText.vue'
export default defineComponent({
  components: {
    RoundText
  },
  props: {
    color: String,
    bgcolor: String,
    time: String,
    src: String,
    alt: String
  },
  emits: ['open'],
  setup (_, context) {
    const open = () => {
      context.emit('open')
    }
    return { open }
  }
})
